
.navigation__link {
  text-decoration: none;
  color: #000000;
  font-family: $oswald-font;
  font-size: 20px;
  line-height: 20px;
  text-transform: uppercase;

  &:hover {
    opacity: 0.6;
  }

  &:active {
    opacity: 0.3;
  }

  &--active,
  &--active:hover,
  &--active:active {
    opacity: 1;
  }
}

.navigation__list {
  @include list;
}

.navigation {
  display: block;
}

@media (max-width: $mobile-width-only) {
  .navigation__item {
    border-top: 1px solid #e6e6e6;
  }

  .navigation__link {
    display: block;
    padding: 22px 20px 24px 20px;
    text-align: center;
  }
}

@media (min-width: $tablet-width) {
  .navigation {
    display: block;
  }

  .navigation__list {
    display: flex;
  }

  .navigation__item {
    margin-left: 33px;
  }

  .navigation__link {
    &--active {
      border-bottom: 2px solid $basic-green;
    }
  }
}


@media (min-width: $desktop-width) {
  .navigation {
    &--index {
      .navigation__link {
        color: #ffffff;

        &--active {
          border-bottom: 2px solid #ffffff;
        }
      }
    }
  }
}
